@import url('https://fonts.googleapis.com/css2?family=Alegreya:wght@500&family=Manrope:wght@400;600&display=swap');


body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrap {
  background-image: url(../public/fon.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  height: 100vh;
}

.container {
  text-align: center;
  width: 516px;
  height: 608px;
  background-color: #fff;
  margin: auto;
  border-radius: 20px;
  box-shadow: -10px 7px 20px rgba(0, 0, 0, 0.25);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

}

.container img {
  margin-top: 56px;
}

.form {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
  width: 343px;
  margin: 0 auto;
}

.forgot {
  text-align: right;
  margin: 0 auto;
  width: 343px;
  margin-bottom: 42px;
}

.text-change {
  font-family: "Manrope";
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 19px;

  color: #989898;
  margin-bottom: 4px;
}



.inputname {
  height: 32px;
  border: 1px solid #E5E5E5;
  padding-left: 13px;
}



.inputpassword {
  height: 32px;
  border: 1px solid #E5E5E5;
  padding-left: 13px;
}

.name {
  margin-bottom: 19px;
}

.name:focus-within .focusing {
  color: #10100F;
}

.password:focus-within .focusing {
  color: #10100F;
}

.password {
  margin-bottom: 11px;
}

.forgot-link {
  font-family: 'Manrope'; 
font-style: normal;
font-weight: 600;
font-size: 16px;
line-height: 22px;
text-decoration: none;
/* identical to box height */


color: #F6B52E;
}

.btn-link {
  width: 343px;
  height: 44px;
  background: #F2B705;
  border-radius: 5px;
  border: 0;
  font-family: 'Manrope';
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 27px;
  cursor: pointer;

  color: #FFFFFF;
}

.btn {
  margin-bottom: 90px;
}

.title {
  font-family: 'Manrope';
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 27px;

  color: #989898;
  margin-top: 40px;
  margin-bottom: 47px;
}

.phone {
  height: 32px;
}

.inputphone {
  margin-bottom: 10px;
}

.error {
  color: red;
  font-family: "Manrope";
  opacity: 1;
  display: none;
}

.error-active {
  display: block;
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translate(-50%);
}